@import url("https://fonts.googleapis.com/css?family=Roboto+Slab:100,300,400,700");

* {
  box-sizing: border-box;
  text-align: center;
  padding: 10px;
  margin: 10;
  background-position: center;
  background-size: 300px 100px;
  background-color: #d2dbdd;
  font-family: "josefin sans", "Arial Narrow", Arial, sans-serif;
  
}


.main_div {
    width : 100%;
    height: 100vh;
    background: #8e44ad;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    text-align: center;
    
}

.center_div {
    width: 25%;
    height: 80vh;
    background-color: #f4f4f4;
    box-shadow: 5px 5px 25px -5px rgba(0, 0, 0, 0.5);
    border-radius: 15px;
}


 


input {
    text-align: center;
    height: 30px;
    top: 10px;
    border:none;
    background: transparent;
    font-size: 20px;
    font-weight: 200;
    width:20%;
    border-bottom: 2px solid #8566aa;
    outline:none;
}

.wave {
    opacity: 0.3;
    position: absolute;
    top: 120%;
    left: 50%;
    background: white;
    width: 50rem;
    height: 50rem;
    margin-left:-25rem;
    margin-top:-20rem;
    transform-origin: 50% 48%;
    border-radius: 43%;
    animation: drift 300ms infinite linear;
    z-index: 1;
    }

    .wave--three {
        animation: drift 5000ms infinite linear;
        z-index: 2 !important;
        opacity: 0.2 !important;
    }

    .wave--two {
        animation: drift 7000ms infinite linear;
        opacity: 0.1 !important;
        z-index: 3 !important;
        
    }